body {
  margin: 0;
  font-family: 'PingFang SC', 'Lantinghei SC', 'Helvetica Neue', 'Microsoft Yahei', 'Hiragino Sans GB', 'Microsoft Sans Serif', 'WenQuanYi Micro Hei', sans;
  color: #666;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.relative{
  position: relative;
}
.abs{
  position: absolute;
}

.pull-right {
  float: right;
}

.pull-left {
  float: left;
}

.clear-fix:after {
  content: " ";
  display: table;
  clear: both;
}

.hide {
  display: none;
}

.red {
  color: red;
}

.table {
  display: table-cell;
  vertical-align: middle;
}


/*text icon */

.text-icon {
  font-style: normal;
  width: 16px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border: 1px solid;
  display: inline-block;
  margin-left: 3px;
  &.jian {
    background: #f07373;
    border-color:#f07373;
    color: white;
  }
  &.shou {
    background: #70bc46;
    border-color: #70bc46;
    color: white;
  }
  &.piao {
    color: #9071CB;
  }
  &.bao {
    color: #4B9A18;
  }
  &.fu {
    color: #FF4E00;
  }
}
